export function getIdKeyForGroupBy(groupBy = {}) {
    if (groupBy.account) {
        return 'account';
    }
    if (groupBy.cluster) {
        return 'cluster';
    }
    if (groupBy.instance_type) {
        return 'instance_type';
    }
    if (groupBy.node) {
        return 'node';
    }
    if (groupBy.org_unit_id) {
        return 'org_unit_id';
    }
    if (groupBy.project) {
        return 'project';
    }
    if (groupBy.region) {
        return 'region';
    }
    if (groupBy.resource_location) {
        return 'resource_location';
    }
    if (groupBy.service) {
        return 'service';
    }
    if (groupBy.service_name) {
        return 'service_name';
    }
    if (groupBy.subscription_guid) {
        return 'subscription_guid';
    }
    return 'date';
}
