export const getComputedReportItemType = (perspective) => {
    let result;
    switch (perspective) {
        case "ocp_supplementary" /* ocpSupplementary */:
            result = "supplementary" /* supplementary */;
            break;
        case "aws" /* aws */:
        case "all_cloud" /* allCloud */:
        case "aws_cloud" /* awsCloud */:
        case "azure" /* azure */:
        case "azure_cloud" /* azureCloud */:
        case "gcp" /* gcp */:
        case "ocp" /* ocp */:
        case "ocp_usage" /* ocpUsage */:
        default:
            result = "cost" /* cost */;
            break;
    }
    return result;
};
