export const getRouteForQuery = (history, query, reset = false) => {
    // Reset pagination
    if (reset) {
        query.filter = Object.assign(Object.assign({}, query.filter), { offset: baseQuery.filter.offset });
    }
    return `${history.location.pathname}?${getQueryRoute(query)}`;
};
export const isAwsAvailable = (awsProviders, awsProvidersFetchStatus, userAccess) => {
    let result = false;
    if (awsProvidersFetchStatus === 2 /* complete */) {
        const data = userAccess.data.find(d => d.type === "aws" /* aws */);
        const isUserAccessAllowed = data && data.access;
        // providers API returns empty data array for no sources
        result =
            isUserAccessAllowed &&
                awsProviders !== undefined &&
                awsProviders.meta !== undefined &&
                awsProviders.meta.count > 0;
    }
    return result;
};
